import React, {FC} from 'react';
import {useForm, Controller} from 'react-hook-form';
import { Input } from 'antd';
import classes from './Register.module.less'
import Title2 from '../../ui/Title2/Title2';



const Register: FC = () => {

  const {
    register,
    formState: {
      errors,
      isValid
    },
    setError,
    reset,
    handleSubmit,
    control

  } = useForm({
    mode: 'all'
  })

  const onSubmit = (data: any) => {
    console.log(data)
  }

  return (
    <div className={classes.register}>
      <Title2 text="Регистрация автовладельца"/>
      <div className="container">
        <div className={classes.container}>
          <div className={classes.body}>
            <form onSubmit={handleSubmit(onSubmit)}>
              <label>
                Логин:
                <Controller rules={{
                  required: 'Введите логин'
                }}
                            control={control}
                            render={({ field }) => <Input {...field}/>} name="login"/>
              </label>
              <label>
                Пароль:
                <Controller rules={{
                  required: 'Введите пароль'
                }}
                            control={control}
                            render={({field}) => <Input.Password {...field}/>} name="password"/>
              </label>
              <label>
                Повторите пароль:
                <Input.Password visibilityToggle={false} {...register('password')}/>
              </label>
              <label>
                Имя:
                <Input {...register('name')}/>
              </label>
              <label>
                Email:
                <Input {...register('email')}/>
              </label>
              <label>
                Номер телефона:
                <Input {...register('phone')}/>
              </label>
              <input className={classes.submit} type="submit"/>
            </form>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Register